.logo {
  height: 90px;
  scale: 2.5;
}
@media screen and (max-width: 1280px) {
  .logo {
    margin-left: 10px;
    height: 70px;
  }
}
@media screen and (max-width: 780px) {
  .logo {
    height: 50px;
  }
}

@media screen and (max-width: 630px) {
  .logo {
    display: none;
  }
}
